<template>
  <div class="total-album-container">
    <div class="album-box">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        :immediate-check="false"
        @load="getClassifyAlbumList"
      >
        <div
          v-for="album in albumList"
          :key="album._id"
        >
          <div @click="clickAlbum(album)" class="content-area">
            <BaseListLayout
              :album="album"
            />
          </div>
          <div class="split-line-1" />
        </div>
      </van-list>
    </div>
  </div>
</template>

<script>
import BaseListLayout from '@/components/homePageLayout/BaseListLayout.vue';
import { ApiAlbumListByClassifyid } from '../../service/api/program';
import { toAlbumPage } from '@/utils/proxyRouteSkip';
import Vue from 'vue';
import { Toast, List } from 'vant';

Vue.use(Toast).use(List);

export default {
  components: {
    BaseListLayout
  },
  
  data() {
    return {
      albumList: [],
      pageSize: 20,
      pageNum: 0,
      loading: false,
      finished: true
    }
  },

  created() {
    this.setDocumentTitle();
  },

  mounted() {
    this.getClassifyAlbumList();
  },

  methods: {
    // todo 设置文档标题
    setDocumentTitle() {
      const classifyTitle = this.$route.query.hasOwnProperty('classifyTitle') ? decodeURIComponent( this.$route.query.classifyTitle ) : '';
      document.title = classifyTitle ? classifyTitle : '全部专辑';
    },

    async getClassifyAlbumList() {
      const albumListResponse = await ApiAlbumListByClassifyid(this.$route.query.classifyId, this.pageNum, this.pageSize);
      if (!albumListResponse || albumListResponse.type === 'fail') return Toast( '服务器错误，请联系客服' );
      this.albumList = this.albumList.concat( albumListResponse.data );

      if (albumListResponse.data.length < this.pageSize || albumListResponse.data.length === 0) {
        this.finished = true;
      } else {
        this.pageNum += 1;
        this.finished = false;
      }
      this.loading = false;
    },

    clickAlbum(album) {
      toAlbumPage(album.chargeType, album.isBuy, album._id);
    }
  }
}
</script>

<style lang="scss" scoped>
.total-album-container {
  width: 100%;
  background: #fff;

  .album-box {
    width: 100%;
    padding: 0 16px;
    box-sizing: border-box;

    .content-area {
      padding: 20px 0;
    }
  }

  .split-line-1 {
    width: 343px;
    height: 1px;
    background: rgba(233,233,233,1);
  }
}
</style>


